<template>
  <v-row justify="center">
    <v-dialog v-model="dialog" persistent max-width="400">
      <template v-slot:activator="{ on, attrs }">
        <v-btn :color="typeColorComputed" v-bind="attrs" v-on="on">
          chang
        </v-btn>
      </template>
      <v-card>
        <v-card-title class="text-h" v-text="title"> </v-card-title>
        <transition name="slide-fade">
          <v-color-picker
            id="picker-color"
            v-show="showPickerColor"
            elevation="0"
            hide-inputs
            v-model="color"
          >
          </v-color-picker>
        </transition>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="secondary" text @click="dialog = false"> Close </v-btn>
          <v-btn color="green darken-1" text @click="saveColor"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template><script>
import VueCookies from "vue-cookies";

export default {
  props: ["title", "typeMode", "oldColor", "typeColor"],
  watch: {
    newColor(color) {
      return color;
    },
  },
  computed: {
    typeColorComputed() {
      return this.typeColor;
    },
    oldColorComputed() {
      return this.oldColor;
    },
    color: {
      get() {
        return this[this.type];
      },
      set(v) {
        this.newColor = v;
        this.setColor(v);
        this[this.type] = v;
      },
    },
  },
  data() {
    return {
      newColor: this.oldColor,
      type: "hex",
      hex: this.$vuetify.theme.themes.dark.primary,
      showPickerColor: true,
      dialog: false,
    };
  },
  methods: {
    saveSetting(setting) {
      console.log(setting);
      this.$store.commit("SET_SETTING", setting);
      VueCookies.remove("setting");
      VueCookies.set("setting", setting);
      this.dialog = false;
    },
    saveColor() {
      var setting = this.$store.getters.setting;
      if (this.typeMode == "dark-primary") {
        setting.primaryDark = this.newColor;
        this.saveSetting(setting);
      }
      if (this.typeMode == "dark-secondary") {
        setting.secondaryDark = this.newColor;
        this.saveSetting(setting);
      }
      if (this.typeMode == "light-primary") {
        setting.primaryLight = this.newColor;
        this.saveSetting(setting);
      }
      if (this.typeMode == "light-secondary") {
        setting.secondaryLight = this.newColor;
        this.saveSetting(setting);
      }
    },
    setColor(color) {
      if (this.typeMode == "dark-primary") {
        this.$vuetify.theme.themes.dark.primary = color;
      }
      if (this.typeMode == "dark-secondary") {
        this.$vuetify.theme.themes.dark.secondary = color;
      }
      if (this.typeMode == "light-primary") {
        this.$vuetify.theme.themes.light.primary = color;
      }
      if (this.typeMode == "light-secondary") {
        this.$vuetify.theme.themes.light.secondary = color;
      }
    },
  },
};
</script>
<style scoped>
#picker-color {
  margin: 50px;
}
</style>